@import url('https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap');
@use 'reset' {}

body {
  margin: 0;
  background-color: #F5F5F5; }
h1, h2, h3 {
  margin: 0; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  font-family: 'Chivo', sans-serif;
  color: black;
  max-width: 100%;
  font-weight: 300; }

.loadContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 92vh;
  width: 100vw;
  background-color: #F5F5F5;
  font-size: 15vw; }
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: spin 1s linear 0s infinite;
  transform-origin: center; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.search {
  height: 8vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 6%;
  margin-bottom: 2vh;
  .searchSubmit {
    background-color: transparent;
    border: none;
    font-size: 2vw;
    padding-top: .5%; }
  input {
    background-color: transparent;
    border: none;
    font-size: 2vw;
    font-family: 'Chivo', sans-serif; }
  input:focus {
    outline: none; } }
